import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }

  add(x,y){
    alert(parseInt(x)+parseInt(y));
  }
  sub(x,y){
    alert(parseInt(x)-parseInt(y));
  }
  mult(x,y){
    alert(parseInt(x)*parseInt(y));
  }
  div(x,y){
    alert(parseInt(x)/parseInt(y));
  }

  str = " ";
  cal(a){
    this.str += a;
  }

}
